
h2.title {
  position: absolute;
  display: inline-block;
  background: #000;
  -webkit-transform: skew(-10deg) rotate(-10deg) translate(-50%, -50%);
  transform: skew(-10deg) rotate(-10deg) translate(-50%, -50%);
  padding: 12px 5px;
  margin: 0;
  top: 100px;
  text-transform: uppercase;
  font-weight: 400;
  width: 200px;
  text-align: center;
}
@media only screen and (min-width: 480px) {
.intro {
    width: 100vw;
    height: 100vh;
    text-align: center;
    color: #fff;
    position: relative;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    z-index: 100;
}
#sectionl .fp-bg {
    background-image: url("../assets/bg.jpg");
    background-size: cover;
    background-position: center 30%;
    width: 100vw;
    height: 100vh;
    -webkit-filter: blur(1px);
    -moz-filter: blur(1px);
    -o-filter: blur(1px);
    -ms-filter: blur(1px);
    filter: blur(1px);
}
#spiders {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
}
.logo-container {
    position: absolute;
    top: 0;
    height: 100vh;
    width: 100vw;
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
        -ms-flex-direction: column;
            flex-direction: column;
}
.logo-container .logo {
    -webkit-animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
    animation: scale-up-center 0.4s cubic-bezier(0.39, 0.575, 0.565, 1) both;
    width: 20vw;
    margin: 0 auto;
}
.logo-container .logo line,
  .logo-container .logo path {
    box-shadow: -5px -5px 5px #888;
}
article.nome {
    font-family: "Dancing Script", "Julius Sans One", "Quicksand", 'Text Me One', sans-serif;
    top: 30%;
    left: calc(50% - 300px);
    font-size: 4em;
    font-weight: 500;
    text-shadow: 0 0.3rem 0.9rem rgba(0,0,0,0.4);
    -webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
    animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}
article.dev {
    font-family: "Dancing Script", "Julius Sans One", "Quicksand", 'Text Me One', sans-serif;
    font-size: 3em;
    text-shadow: 0 0.3rem 0.9rem rgba(0,0,0,0.4);
    -webkit-animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
    animation: tracking-in-expand 0.7s cubic-bezier(0.215, 0.61, 0.355, 1) both;
}
}
@media only screen and (max-device-width: 480px) {
.intro {
    padding: 0;
}
.logo-container .logo {
    min-width: 150px;
    width: 50vw;
}
.logo-container .nome {
    font-size: 8.2em;
}
.logo-container .dev {
    font-size: 6.5em;
}
}


