
.logo-menu {
  height: 45px;
  width: 35px;
  position: absolute;
  top: 5px;
  left: 10px;
}
#menu {
  position: fixed;
  width: 100vw;
  height: 55px;
  top: 0;
  left: 0;
  background-color: rgba(0,0,0,0.44);
  box-shadow: 0 16px 24px 2px rgba(0,0,0,0.14), 0 6px 30px 5px rgba(0,0,0,0.12), 0 8px 10px -5px rgba(0,0,0,0.3);
}
#menu ul {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
#menu ul li {
  font-size: 18pt;
  height: 55px;
}
#menu ul li.active a {
  color: #00bd79;
}
#menu ul li a {
  padding: 16px;
  display: block;
  text-decoration: none;
  color: #fff;
  -webkit-transition: 0.3s all linear;
  transition: 0.3s all linear;
}
#menu ul li a:link {
  text-decoration: none;
}
#menu ul li a:hover,
#menu ul li a:active {
  color: #00bd79;
}
@media only screen and (max-device-width: 480px) {
.logo-menu {
    height: 75px;
    width: 55px;
}
#menu {
    height: 95px;
}
#menu ul li {
    height: 95px;
    font-size: 45pt;
}
}


