
h2.title {
  position: absolute;
  display: inline-block;
  background: #000;
  -webkit-transform: skew(-10deg) rotate(-10deg) translate(-50%, -50%);
  transform: skew(-10deg) rotate(-10deg) translate(-50%, -50%);
  padding: 12px 5px;
  margin: 0;
  top: 100px;
  text-transform: uppercase;
  font-weight: 400;
  width: 200px;
  text-align: center;
}
@media only screen and (min-width: 480px) {
#section2 {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
        -ms-flex-pack: center;
            justify-content: center;
    height: 100vh;
}
#section2 h2.title {
    background-color: #00bd79;
    color: #fff;
    left: 50%;
    font-size: 23pt;
    box-shadow: 0 16px 24px 2px rgba(0,0,0,0.14), 0 6px 30px 5px rgba(0,0,0,0.12), 0 8px 10px -5px rgba(0,0,0,0.3);
    top: -20px;
}
#section2 .content-contato {
    position: absolute;
    max-width: calc(100vh - 100px);
    background-color: #fff;
    top: 130px;
    left: 50px;
    right: 50px;
    height: calc(100vh - 160px);
    margin: 0 auto;
}
#section2 .content-contato .dados-contato {
    font-family: "Dancing Script", "Julius Sans One", "Quicksand", 'Text Me One', sans-serif;
    font-size: 20pt;
    min-height: 540px;
    max-height: calc(100vh - 160px);
}
#section2 .content-contato .dados-contato span {
    top: 20px;
    display: block;
    text-align: center;
    line-height: 13vh;
    position: relative;
    color: #7d7a7a;
}
#section2 .content-contato .dados-contato span a {
    color: #7d7a7a;
}
#section2 .content-contato .dados-contato span i:before {
    font-size: 30pt;
}
#section2 .content-contato .dados-contato span.fone {
    font-size: 2vw;
    top: 40px;
}
#section2 .content-contato .dados-contato span.face,
  #section2 .content-contato .dados-contato span.git,
  #section2 .content-contato .dados-contato span.email {
    font-size: 3vw;
}
}
@media only screen and (max-device-width: 480px) {
#section2 h2.title {
    width: 350px;
    left: 50%;
    font-size: 40pt;
}
#section2 .content-contato {
    max-width: 100vw;
    top: 200px;
    height: calc(100vh - 370px);
}
#section2 .content-contato .dados-contato {
    font-family: "Dancing Script", "Julius Sans One", "Quicksand", 'Text Me One', sans-serif;
    font-size: 20pt;
    height: auto;
    margin-top: 50px;
}
#section2 .content-contato .dados-contato span {
    top: 40px;
}
#section2 .content-contato .dados-contato span i:before {
    font-size: 40pt;
}
#section2 .content-contato .dados-contato span.fone,
  #section2 .content-contato .dados-contato span.face,
  #section2 .content-contato .dados-contato span.git,
  #section2 .content-contato .dados-contato span.email {
    font-size: 8vw;
}
}


